kground-color:lightpink;grid-column:8/13;grid-row:4/18}body .epics-team-builder .setup-page .preferred-project-slider{display:flex;flex-direction:row;justify-content:center}body .epics-team-builder .setup-page .preferred-project-slider h5{margin-top:16px;font-family:'Times New Roman', Times, serif}body .epics-team-builder .setup-page .preferred-project-slider .noUi-target{display:block;margin-top:18px;margin-left:3rem;width:16rem;height:0.5rem;border:1px solid #aaa}body .epics-team-builder .setup-page .preferred-project-slider .noUi-target .noUi-handle{border:1px solid #aaa;height:1.3rem;width:28px;left:-14px;top:-6px}body .epics-team-builder .setup-page .preferred-project-slider .noUi-target .noUi-handle::before{left:10px;background:#aaa}body .epics-team-builder .setup-page .preferred-project-slider .noUi-target .noUi-handle::after{left:16px;background:#aaa}body .epics-team-builder .setup-page .preferred-project-slider .noUi-target .noUi-marker{height:0.3rem}body .epics-team-builder .setup-page .preferred-project-slider .noUi-target .noUi-value{font-size:0.8rem;color:black}body .epics-team-builder .gray-background{position:absolute;height:90%;width:100%;z-index:-1;background-color:#ececec;top:10%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

